:root {
  --primary-color: #006FB2;
  --secondary-color: #0D0D0D;
  --large: 2em;
  --medium: 1.25em;
  /* primary colors */
  --black: #000000; 
  --lt-gray: #DDDDDD;
  --md-gray: #676767;
  --dk-gray: #444444;
  --cod-gray: #0D0D0D;
  /* secondary colors -  */
  --lt-mine-shaft: #333333;
  --dk-mine-shaft: #222222;
  --white: #FFFFFF;
  /* fonts */
  --primary-font: "Visby DemiBold";
  --line-spacing: 1.5em;
  /* logo clear space: 10% width */
  /* logo on bg img: add black layer with 60% transparency */

}

* {
  box-sizing: border-box;
}

body {
  background: #f0f0f0;
  margin: 0;
  padding: 0;
  font-family: 'Rajdhani', sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.App {
  background: var(--white);
  margin: 0 auto;
  max-width: 1024px;
}
.contact-bar {
  background: var(--primary-color);
  text-align: right;
  padding: 0.5em;
}

.link {
  color: var(--lt-gray);
  display: inline-block;
  text-decoration: none;
  margin-right: 3em;
  font-weight: bold;
  -webkit-transition: color 300ms;
  transition: color 300ms;
}

.link:hover {
  color: var(--white);
}
.nav-bar {
  align-items: center;
  background: var(--secondary-color);
  color: white;
  display: flex;
  justify-content: space-around;
  height: 6em;
  padding: 0.5em;
  -webkit-transform: scale(1);
          transform: scale(1);
  -webkit-transition: all 200ms;
  transition: all 200ms;
  z-index: 1;
}
.small-nav-bar {
  height: 4em;
  -webkit-transform: scale(0.75);
          transform: scale(0.75);
}
.nav-image {
  height: 100%;
}
.nav-link {
  color: var(--lt-gray);
  font-size: var(--medium);
  text-decoration: none;
  -webkit-transition: color 300ms;
  transition: color 300ms;
  padding: 1em;
}
.nav-link:hover, .nav-link-active {
  color: #f8eda1;
}
@media (min-width: 768px) {
  .nav-link {
    font-size: var(--large);
  }
}
.header {
  position: -webkit-sticky;
  position: sticky;
  top: 0;
  background: var(--secondary-color);
  z-index: 2;
}
.button {
  background: var(--primary-color);
  color: var(--white);
  cursor: pointer;
  display: inline-block;
  text-transform: uppercase;
  margin: 0 0.25em 0.25em;
  padding: 0.5em 1em;
  text-decoration: none;
  cursor: pointer;
  -webkit-transition: opacity 300ms;
  transition: opacity 300ms;
}
.button:hover {
  opacity: 0.8;
}
.splash-container {
  background: url(/gemini-group/static/media/banner1.c02fcb0b.jpg);
  background-repeat: no-repeat;
  background-attachment: fixed;
  padding: 2em 4em;
  -webkit-transition: all 200ms;
  transition: all 200ms;  
  text-align: center;
}
.splash-title, .splash-subtitle {
  display: inline-block;
  padding: 0.1rem 0.5rem;
  z-index: -1;
}
.splash-title {
  margin-top: 3em;
  font-size: var(--large);
  margin-bottom: 0.5em;
  z-index: -1;
}
.splash-subtitle {
  font-size: var(--medium);
  margin-bottom: 1em;
}
.splash-button {
  background: var(--primary-color);
  color: var(--white);
  display: inline-block;
  font-size: 1.5em;
  margin: 0 0.5em 0 0;
  padding: 0.5em 1em;
  text-decoration: none;
}
.separator {
  margin-top: 2em;
}

@media (min-width: 768px) {
  .splash-container {
    text-align: left;
  }
  .splash-title {
    margin-top: 0.5em !important;
    font-size: 3em;
  }
  .splash-subtitle {
    font-size: 2em;
  }
  .splash-title, .splash-subtitle {
    background: rgba(0, 0, 0, 0.5);
    color: var(--white);
    text-align: left;
  }
  .separator {
    display: none;
  }
}
.register {
  padding: 1em;
  text-align: center;
}
.info {
  display: flex;
  width: 100%;
  align-items: center;
  justify-content: center;
  flex-wrap: wrap;
}
.info-text-container {
  flex-grow: 1;
  flex-basis: 300px;
  padding: 0 2em 1em;
}
.info-text {
  line-height: 1.75em;
}
.info-video-container{
  background: #000;
  flex-basis: 400px;
  flex-shrink: 0;
  margin: 0 3em 2em;
}
.reverse {
  flex-direction: row-reverse;
}

@media (min-width: 768px) {
  .info-video-container{
    margin: 2em 3em;
  }
}
.hero {
  padding: 2em;
  position: relative;
  background-repeat: no-repeat;
  background-size: cover;
}
.hero-content{
  position: relative;
  height: 100%;
  display: flex;
  flex-direction: column;
  align-items: center;
  align-content: center;
  justify-content: center;
  color: white;
  z-index: 1;
}
.hero-cover {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
}
.footer {
  padding: 2em;
  background: var(--black);
  color: var(--lt-gray);
  flex-wrap: wrap;
}
.footer>div {
  flex-basis: 50%;
}

.footer-columns {
  width: 100%;
  display: flex;
  justify-content: space-between;
}

.footer-menu-title {
  font-size: 1rem;
  text-transform: uppercase;
}

.footer-link {
  display: block;
  color: var(--white);
  text-decoration: none;
  margin: 0.5em 0;
  -webkit-transition: color 300ms;
  transition: color 300ms;
}

.footer-link:hover {
  color: #f8eda1;
}

.footer-label {
  display: flex;
  width: 100%;
  justify-content: space-between;
  margin-bottom: 1em;
}
.footer-input {
  margin-left: 1em;
}
.footer-bottom {
  font-size: 0.75em;
}
